import React, { useEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import KeranjangDetail from '../components/KeranjangDetail';
import Loading from '../components/Loading';
import Message from '../components/Message';
import TitlePage from '../components/TitlePage';
import { getKeranjangByPemesananId, getPemesananById, getTotalByPemesananId } from '../utils/api';

const DetailItemPage = () => {
  const titlePage = "Detail Item";
  const iconPage = "add_shopping_cart";
  const navigate = useNavigate();
  const [isLoading,setIsLoading] = useState(false);

  const {pemesananId} = useParams();
  const [keranjang,setKeranjang] = useState({}) 
  const [pemesanan,setPemesanan] = useState({}) 
  const [total,setTotal] = useState('') 

  useEffect(()=>{
    getPemesananByIdReqFunc()
    getKeranjangByPemesananIdFunc();
    getTotalByPemesananIdFunc()
  },[])

  const getKeranjangByPemesananIdFunc = async () => {
    setIsLoading(true);
    const reqGetKeranjangByPemesananId = await getKeranjangByPemesananId(pemesananId);
    setIsLoading(false);
    setKeranjang(reqGetKeranjangByPemesananId);
  }

  const getPemesananByIdReqFunc = async () => {
    const getPemesananByIdReq = await getPemesananById(pemesananId);
    setPemesanan(getPemesananByIdReq);
  }

  const getTotalByPemesananIdFunc = async () => {
    const getTotalByPemesananIdReq = await getTotalByPemesananId(pemesananId);
    setTotal(getTotalByPemesananIdReq.data[0].total);
  }

  if(isLoading) return <Loading/>

  if(!keranjang.success)
    return (<>
      <TitlePage titlePage={titlePage} iconPage={iconPage}/>
      <Message message={keranjang.message}/>    
    </>)
  
  return (<>
    <TitlePage titlePage={titlePage} iconPage={iconPage}/>
    <span style={{fontSize:'20px',fontWeight:'bold'}}>No.Pemesanan : {pemesanan.data.no_pesan}</span>
    <KeranjangDetail 
        items={keranjang.data} 
        total={total} 
    />
    <button onClick={()=>{navigate(`/pemesanan/${pemesananId}`)}} className="waves-effect waves-light btn-small">Kembali</button>
  </>);
}

export default DetailItemPage;