import {useEffect, useState} from 'react';
import { useParams } from 'react-router-dom';
import CardItem from '../components/CardItem';
import KeranjangInput from '../components/KeranjangInput';
import Loading from '../components/Loading';
import Message from '../components/Message';
import TitlePage from '../components/TitlePage';
import { getMenuById } from '../utils/api';

const MenuDetailPage = () => {  
    const titlePage = "Menu Katering";
    const iconPage = "assignment";
    const [isLoading,setIsLoading] = useState(true);
    const [menu,setMenu] = useState({});
    const {menuId} = useParams();

    useEffect(() => {
        getMenuByIdFunc();
    },[])
    
    const getMenuByIdFunc = async () => {
        const menu = await getMenuById(menuId);
        setMenu(menu);
        setIsLoading(false);
    }

    const DetailMenu = () => {
        if(menu.data.diskon > 0){
            return (
                <ul className="collection">
                    <li className="collection-item">Harga Sebelum Diskon : <span  style={{textDecoration:'line-through'}}>Rp.{menu.data.harga}</span></li>
                    <li className="collection-item" style={{fontSize:'20px',fontWeight:'bold'}}>Harga Diskon : Rp.{menu.data.harga-((menu.data.diskon/100)*menu.data.harga)}</li>
                </ul>
            )
        }
        return (
            <ul className="collection" style={{fontSize:'20px',fontWeight:'bold'}}>
                <li className="collection-item">Harga : Rp.{menu.data.harga}</li>
            </ul>
        )
    }

    if(isLoading) return <Loading/>

    if(!menu.success)
        return (
            <>
            <TitlePage titlePage={titlePage} iconPage={iconPage}/>
            <Message message={menu.message}/>
            </>
        )

    return (
        <>
            <TitlePage titlePage={titlePage} iconPage={iconPage}/>
            <div className="row">    
                <div className="col s12 m8 offset-l2">
                    <CardItem
                        key={menu.data.id}
                        id={menu.data.id}
                        imageUrl={menu.data.gambar}
                        title={menu.data.diskon>0 ? menu.data.nama+" (Diskon : "+menu.data.diskon+"%)": menu.data.nama}
                        body={menu.data.detail}
                    />
                    <DetailMenu/>
                    <KeranjangInput menuId={menuId}/>
                </div>
            </div>
        </>
    )
}

export default MenuDetailPage;