import React from 'react';
import { Link } from 'react-router-dom';
import { UserConsumer } from '../contexts/UserContext';
import sidenavImg from '../images/sidenav.jpg';
import logo from '../images/tomboluwe.jpg';

const Navigation = () => {  
  const NavList = ({authedUser}) => {
    if(authedUser == null)
      return (
        <>
          <li><Link to="/" className="sidenav-close">Home</Link></li>
          <li><Link to="/menu" className="sidenav-close">Menu Katering</Link></li>
          <li><Link to="/register" className="sidenav-close">Register</Link></li>
          <li><Link to="/login" className="sidenav-close">Login</Link></li>
        </>    
      )  
    return (
      <>
        <li><Link to="/" className="sidenav-close">Home</Link></li>
        <li><Link to="/menu" className="sidenav-close">Menu Katering</Link></li>
        <li><Link to="keranjang" className="sidenav-close">Keranjang</Link></li>
        <li><Link to="/pemesanan" className="sidenav-close">Pemesanan</Link></li>
        <li><Link to="/logout" className="sidenav-close">Logout</Link></li>
      </>
    )
  }

  return (
    <UserConsumer>
      { ({authedUser,setAuthedUser}) => {
        const userId = authedUser!=null?authedUser.user_id:'Tombo Luwe Catering';
        const userName = authedUser!=null?authedUser.nama:'Nikmati hidangan selera cita rasa kamu.';
        
        return (
          <nav className="teal lighten-1" role="navigation">
            <div className="nav-wrapper">
              <a className="sidenav-trigger" href="#!" data-target="mobile-demo"><i className="material-icons right">menu</i></a>
              <a id="logo-container" href="#" className="brand-logo"><img src={logo} className="hide-on-med-and-down" alt="logo" width="30"/> Tombo Luwe</a>
              <ul id="nav-mobile" className="right hide-on-med-and-down">
                <NavList authedUser={authedUser}/>
              </ul>
              <ul className="right on-med-and-down">
                <li></li>
              </ul>
              <ul id="mobile-demo" className="sidenav">
                <li>
                  <div className="user-view">
                    <div className="background">
                      <img src={sidenavImg} alt="sidenav"/>
                    </div>
                    <a><img className="circle" src={logo} alt="profile"/></a>
                    <a><span className="white-text name">{userId}</span></a>
                    <a><span className="white-text email">{userName}</span></a>
                  </div>
                </li>
                <NavList authedUser={authedUser}/>
              </ul>
            </div>
          </nav>
        )
      }}
    </UserConsumer>
  )
}

export default Navigation;