import { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import UserContext from '../contexts/UserContext';
import { login, putAccessToken } from '../utils/api';
import Loading from './Loading';

const LoginInput = () => {
  const navigate = useNavigate();
  const [isLoading,setIsLoading] = useState(false);
  const { authedUser,setAuthedUser } = useContext(UserContext)
  const [user_id,setUserId] = useState('');
  const [password,setPassword] = useState('');

  const onSubmitHandler = async (event) => {
    event.preventDefault();

    setIsLoading(true);

    const reqLogin = await login({
      user_id,
      password,
    });

    setIsLoading(false);

    if(!reqLogin.success){
      M.toast({html: reqLogin.message});
      return
    }
    putAccessToken(reqLogin.token)    
    setAuthedUser(reqLogin.user);
    navigate('/')
  }

  if(isLoading) return <Loading/>

  return (
    <form onSubmit={onSubmitHandler} className='login-input'>
      <input type="text" placeholder='User ID' value={user_id} onChange={(e)=>{ setUserId(e.target.value) }} />
      <input type="password" placeholder='Password' value={password} onChange={(e)=>{ setPassword(e.target.value) }} />
      <button className="waves-effect waves-light btn-small">Masuk</button>
    </form>
  );
}

export default LoginInput;