import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import KeranjangDetail from '../components/KeranjangDetail';
import Loading from '../components/Loading';
import Message from '../components/Message';
import ModalConfirm from '../components/ModalConfirm';
import TitlePage from '../components/TitlePage';
import { addPemesanan, deleteKeranjang, getKeranjangAll, getTotalKeranjangByPelanggan } from '../utils/api';
import { minDate } from '../utils/helpers';

const KeranjangPage = () => {
  const titlePage = "Keranjang";
  const iconPage = "add_shopping_cart";
  const navigate = useNavigate();
  const [isLoading,setIsLoading] = useState(false);

  const [tgl_antar,setTglAntar] = useState('') 
  const [ket,setKet] = useState('') 
  const [keranjang,setKeranjang] = useState({}) 
  const [total,setTotal] = useState('') 
  const [minTglAntar,setMinTglAntar] = useState('') 

  useEffect(()=>{
    getKeranjangAllFunc();
    getTotalKeranjangByPelangganFunc()
    setMinTglAntar(minDate(3));
  },[])

  const onSubmitHandler = async () => {
    setIsLoading(true);
    const reqAddPemesanan = await addPemesanan({
      tgl_antar,
      ket,
    });
    setIsLoading(false);

    if(!reqAddPemesanan.success){
      M.toast({html: reqAddPemesanan.message});
      return
    }

    navigate('/pemesanan')
  }

  const onDelete = async (id) => {
    const reqDeleteKeranjang = await deleteKeranjang(id);
    if(!reqDeleteKeranjang.success){
      M.toast({html: reqDeleteKeranjang.message});
      return
    }
    await getKeranjangAllFunc();
  }

  const getKeranjangAllFunc = async () => {
    setIsLoading(true);
    const reqGetKeranjangAll = await getKeranjangAll();
    setIsLoading(false);
    setKeranjang(reqGetKeranjangAll);
  }

  const getTotalKeranjangByPelangganFunc = async () => {
    const reqGetTotalKeranjangByPelanggan = await getTotalKeranjangByPelanggan();
    setTotal(reqGetTotalKeranjangByPelanggan.data[0].total);
  }

  if(isLoading) return <Loading/>

  if(!keranjang.success)
    return (<>
      <TitlePage titlePage={titlePage} iconPage={iconPage}/>
      <Message message={keranjang.message}/>    
    </>)
  
  return (<>
    <TitlePage titlePage={titlePage} iconPage={iconPage}/>
    <form onSubmit={onSubmitHandler} className='login-input'>
      <label>Tanggal Pengiriman</label>
      <input type="datetime-local" min={minTglAntar} placeholder='Tanggal Pengiriman' value={tgl_antar} onChange={(e)=>{ setTglAntar(e.target.value) }} />
      <input type="text" placeholder='Catatan' value={ket} onChange={(e)=>{ setKet(e.target.value) }} />
      <KeranjangDetail 
        items={keranjang.data} 
        total={total} 
        onDelete={onDelete}
      />
      <a className="waves-effect waves-light btn-small modal-trigger" href="#pesan">Pesan</a>
      <ModalConfirm
        header="Konfirmasi"
        body="Apakah yakin akan menyimpan transaksi tersebut?"
        action={onSubmitHandler}
        target="pesan"
      />
    </form>
  </>);
}

export default KeranjangPage;