import React, { useEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import Collection from '../components/Collection';
import Loading from '../components/Loading';
import Message from '../components/Message';
import TitlePage from '../components/TitlePage';
import ImgBelumLunas from '../images/belum_lunas.png';
import ImgLunas from '../images/lunas.png';
import ImgVerif from '../images/verif.png';
import ImgSelesai from '../images/selesai.png';
import { getPemesananById, getTotalByPemesananId } from '../utils/api';
import PembayaranInput from '../components/PembayaranInput';

const PemesananDetailPage = () => {
  const titlePage = "Detail Pemesanan";
  const iconPage = "book";
  const navigate = useNavigate();
  const [isLoading,setIsLoading] = useState(false);
  const {pemesananId} = useParams();

  const [pemesanan,setPemesanan] = useState({}) 
  const [collectionItem,setCollectionItem] = useState([]) 

  useEffect(()=>{
    getPemesananByIdFunc();
  },[])

  const getPemesananByIdFunc = async () => {
        setIsLoading(true);
        const reqGetPemesananById = await getPemesananById(pemesananId);
        setPemesanan(reqGetPemesananById);
        const {
            id,
            no_pesan,
            ket,
            tgl_pesan,
            tgl_antar,
            tgl_bayar,
            tgl_verif,
            tgl_selesai,
        } = reqGetPemesananById.data;

        setCollectionItem([]);
        const reqGetTotalByPemesananId = await getTotalByPemesananId(id);
        const total = reqGetTotalByPemesananId.data[0].total;
        let status = "Belum Lunas";
        let image = ImgBelumLunas;
        
        if(tgl_bayar != null && tgl_verif == null){
            status = "Lunas";
            image = ImgLunas;
        }

        if(tgl_verif != null && tgl_selesai == null){
            status = "Proses Verifikasi";
            image = ImgVerif;
        }

        if(tgl_selesai != null){
            status = "Selesai";
            image = ImgSelesai;
        }
        
        setCollectionItem((prevState)=>[
            ...prevState,{
            id,
            title: no_pesan,
            body: `
                Total : Rp.${total} ( ${status} )<br/>
                Tgl Pesan : ${tgl_pesan.split('T')[0]}<br/>
                Tgl Pengiriman : ${tgl_antar.split('T')[0]}<br/>
                Catatan : ${ket}
            `,
            image,
        }]);

        setIsLoading(false);
  }

  if(isLoading) return <Loading/>

  if(!pemesanan.success)
    return (<>
      <TitlePage titlePage={titlePage} iconPage={iconPage}/>
      <Message message={pemesanan.message}/>    
    </>)

  return (<>
    <TitlePage titlePage={titlePage} iconPage={iconPage}/>
    <Collection items={collectionItem} cursor={true}/>
    <Link to={"/pemesanan/"+pemesananId+"/detail"} className="waves-effect waves-light btn-small">Detail Item</Link>
    <Link to={"/pemesanan/"+pemesananId+"/chat"} className="waves-effect waves-light btn-small">Chat</Link>
    <PembayaranInput reloadParent={getPemesananByIdFunc}/>
  </>);
}

export default PemesananDetailPage;