import { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import UserContext from '../contexts/UserContext';
import { register, putAccessToken } from '../utils/api';
import Loading from './Loading';

const RegisterInput = () => {
  const [isLoading,setIsLoading] = useState(false);
  const navigate = useNavigate();
  const {authedUser,setAuthedUser} = useContext(UserContext);
  const [nama,setNama] = useState('');
  const [alamat,setAlamat] = useState('');
  const [telepon,setTelepon] = useState('');
  const [user_id,setUserId] = useState('');
  const [password,setPassword] = useState('');
  const [password_confirm,setPasswordConfirm] = useState('');

  const onSubmitHandler = async (event) => {
    event.preventDefault();

    setIsLoading(true);
    
    const reqRegister = await register({
      nama,
      alamat,
      telepon,
      user_id,
      password,
      password_confirm,
    })
    
    setIsLoading(false);

    if(!reqRegister.success){
      M.toast({html: reqRegister.message});
      return
    }
    putAccessToken(reqRegister.token)
    setAuthedUser(reqRegister.data)    
    navigate('/');
  }

  if(isLoading) return <Loading/>

  return (
    <form onSubmit={onSubmitHandler} className='login-input'>
      <input type="text" placeholder='Nama' value={nama} onChange={(e)=>{ setNama(e.target.value) }} />
      <input type="text" placeholder='Alamat' value={alamat} onChange={(e)=>{ setAlamat(e.target.value) }} />
      <input type="number" placeholder='Telepon' value={telepon} onChange={(e)=>{ setTelepon(e.target.value) }} />
      <input type="text" placeholder='User ID' value={user_id} onChange={(e)=>{ setUserId(e.target.value) }} />
      <input type="password" placeholder='Password' value={password} onChange={(e)=>{ setPassword(e.target.value) }} />
      <input type="password" placeholder='Konfirmasi Password' value={password_confirm} onChange={(e)=>{ setPasswordConfirm(e.target.value) }} />
      <button className="waves-effect waves-light btn-small">Register</button>
    </form>
  );
}

export default RegisterInput;