import { useState } from 'react';
import { useParams } from 'react-router-dom';
import { addPembayaran } from '../utils/api';
import Loading from './Loading';
import ModalInfo from './ModalInfo';
import TitlePage from './TitlePage';

const PembayaranInput = ({reloadParent}) => {
  const [isLoading,setIsLoading] = useState(false);
  const {pemesananId} = useParams();
  const [tgl_bayar,setTglBayar] = useState('');
  const [bukti_bayar,setBuktiBayar] = useState(null);

  const onSubmitHandler = async (event) => {
    event.preventDefault();
    
    setIsLoading(true);
    
    const reqAddPembayaran = await addPembayaran({
      id: pemesananId,
      tgl_bayar,
      bukti_bayar,
    });
    
    setIsLoading(false);

    if(!reqAddPembayaran.success){
      M.toast({html: reqAddPembayaran.message});
      return
    }

    if(reloadParent) reloadParent();
  }

  if(isLoading) return <Loading/>

  return (<>    
    <TitlePage titlePage="Pembayaran" iconPage="monetization_on"/>
    <form onSubmit={onSubmitHandler} className='login-input'>
        <label>Tgl Bayar</label>
        <input type="datetime-local" placeholder='Tgl Bayar' value={tgl_bayar} onChange={(e)=>{ setTglBayar(e.target.value) }} />
        <label>Bukti Bayar</label><br/>
        <input type="file" placeholder='Bukti Bayar' onChange={(e)=>{ setBuktiBayar(e.target.files[0]) }} />
        <br/><br/>
        <div className="col s12">
            <button className="waves-effect waves-light btn-small">Simpan</button>
            <a href="#infoPembayaran" className="waves-effect waves-light btn-small modal-trigger">Info Pembayaran</a>
            <ModalInfo
                header="Info Pembayaran"
                body="Silahkan lakukan pembayaran ke No.Rekening : 425243523213 - Mandiri (a.n. Achmad Haris)"
                target="infoPembayaran"
            />
        </div>
    </form>
</>);
}

export default PembayaranInput;