import { Link } from "react-router-dom";

const Collection = ({items,cursor}) => {
    let cursorStyle = {
        minHeight:"60px"
    }
    
    if(cursor)
        cursorStyle = {
            cursor:"pointer",
            backgroundColor:"rgb(248,248,248)"
        }

    return (<>
        <ul className="collection">
            { items.map((item) => {
                const {
                    id,
                    title,
                    body,
                    image,
                    nav,
                    action,
                } = item;
        
                return (
                    <li onClick={()=>{if(nav) nav(id)}} className="collection-item avatar" key={id} style={cursorStyle}>
                        {image?<img src={image} className="circle"/>:<i className="material-icons circle">account_circle</i>}
                        <span className="title" style={{fontSize:'15px',fontWeight:'bold'}}>{title}</span>
                        <p dangerouslySetInnerHTML={{__html: body}}/>
                        {action?<Link onClick={()=>{ action(id) }} className="secondary-content" style={{color:'red'}}><i className="material-icons">delete</i></Link>:''}
                    </li>
                )
            })}
        </ul>
    </>);
}

export default Collection;