import {useEffect, useState} from 'react';
import CardItem from '../components/CardItem';
import Loading from '../components/Loading';
import Message from '../components/Message';
import { getMenuAll } from '../utils/api';
import { useNavigate } from 'react-router-dom';
import TitlePage from '../components/TitlePage';

const MenuKateringPage = () => {
    const titlePage = "Menu Katering";
    const iconPage = "assignment";
    const navigate = useNavigate();  
    const [menus,setMenus] = useState({});
    const [isLoading,setIsLoading] = useState(true);

    useEffect(() => {
        getMenusAllFunc();
    },[])
    
    const getMenusAllFunc = async () => {
        const menus = await getMenuAll();
        setMenus(menus);
        setIsLoading(false);
    }

    if(isLoading) return <Loading/>

    if(!menus.success)
        return (
            <>
            <TitlePage titlePage={titlePage} iconPage={iconPage}/>
            <Message message={menus.message}/>
            </>
        )

    return (
        <>
        <TitlePage titlePage={titlePage} iconPage={iconPage}/>
        <div className="row">                
            {
                menus.data.map((data) => {
                    const title = data.diskon > 0 ? data.nama +" (Diskon : "+data.diskon+"%)" : data.nama;
                    return (
                        <div className="col s12 m4" key={data.id}>    
                            <CardItem
                                id={data.id}
                                imageUrl={data.gambar}
                                title={title}
                                body={data.detail}
                                onClick={()=>{ navigate(data.id.toString()) }}
                            />
                        </div>
                    )
                })
            }
        </div>
        </>
    )
}

export default MenuKateringPage;