import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import ChatInput from '../components/ChatInput';
import Collection from '../components/Collection';
import Loading from '../components/Loading';
import Message from '../components/Message';
import TitlePage from '../components/TitlePage';
import { getChatByPemesananId, getPemesananById } from '../utils/api';
import { timeStampToDateTime } from '../utils/helpers';

const ChatPage = () => {
  const titlePage = "Chat";
  const iconPage = "chat";
  const navigate = useNavigate();
  const [isLoading,setIsLoading] = useState(false);

  const {pemesananId} = useParams();
  const [pemesanan,setPemesanan] = useState({})
  const [chatsCollection,setChatsCollection] = useState([]) 
  const [chats,setChats] = useState({}) 

  useEffect(()=>{
    getChatByPemesananIdFunc();
  },[])

  const getChatByPemesananIdFunc = async () => {
    setIsLoading(true);
    const getPemesananByIdReq = await getPemesananById(pemesananId);
    setPemesanan(getPemesananByIdReq.data);
    const getChatByPemesananIdReq = await getChatByPemesananId(pemesananId);
    setChats(getChatByPemesananIdReq);
    if(!getChatByPemesananIdReq.success){
        setIsLoading(false);
        return
    }

    setChatsCollection([]);
    await getChatByPemesananIdReq.data.map(async (item) => {
        setChatsCollection((prevState)=>[
            ...prevState, {
            id: item.id,
            title: item.nama.split(' ')[0],
            body: item.chat+'<br/><i>'+timeStampToDateTime(item.created_at)+'</i>',
        }]);
    })
    setIsLoading(false);
  }

  if(isLoading) return <Loading/>
  
  return (<>
    <TitlePage titlePage={titlePage} iconPage={iconPage}/>
    <span style={{fontSize:'15px',fontWeight:'bold'}}>No.Pemesanan : {pemesanan.no_pesan}</span>
    <div style={{height: '68%', overflowY: 'scroll', border:'1px solid #00000'}}>
    {!chats.success?<Message message={chats.message}/>:''}
    <Collection 
        items={chatsCollection} 
    />
    </div>
    <ChatInput reloadParent={getChatByPemesananIdFunc}/>
  </>);
}

export default ChatPage;