import { Link, useNavigate } from "react-router-dom";

const KeranjangDetail = ({items,total,onDelete}) => {
    const navigate = useNavigate();

    return (
        <>
            <ul className="collection">
                { items.map((item) => {
                    const {
                        menu,
                        harga,
                        jumlah,
                        diskon,
                    } = item;
                    const harga_netto = harga - (harga*(diskon/100));
                    return (
                        <li onClick={()=>{navigate(`/menu/${menu.id}`)}} className="collection-item avatar" key={item.id} style={{minHeight:'60px'}}>
                        <img src={menu.gambar} className="circle"/>
                        <span className="title" style={{fontSize:'20px',fontWeight:'bold'}}>{menu.nama}</span>
                        <p>Rp.{harga_netto} x {jumlah}</p>
                        {onDelete?<Link onClick={()=>{onDelete(item.id)}} className="secondary-content" style={{color:'red'}}><i className="material-icons">delete</i></Link>:''}
                        </li>
                    )
                })}
            </ul>
            <ul className="collection" style={{fontSize:'20px',fontWeight:'bold'}}>
                <li className="collection-item">Total : Rp.{total}</li>
            </ul>
        </>
    );
}

export default KeranjangDetail;