export const minDate = (val) => {
    var iso = new Date();
    iso.setDate(iso.getDate() + val);
    iso = iso.toISOString();
    iso = iso.substring(0,iso.length-1).split(':');
    return iso[0]+":"+iso[1];
}

export const timeStampToDateTime = (val) => {
    const nDate = new Date(val).toLocaleString('id-ID', { timeZone: "Asia/Jakarta" });
    return nDate;
}