import React from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';
import Navigation from './Navigation';
import Footer from './Footer';
import HomePage from '../pages/HomePage';
import MenuKateringPage from '../pages/MenuKateringPage';
import RegisterPage from '../pages/RegisterPage';
import LoginPage from '../pages/LoginPage';
import { getSelfUser } from '../utils/api';
import MenuDetailPage from '../pages/MenuDetailPage';
import Logout from './Logout';
import { UserProvider } from '../contexts/UserContext';
import KeranjangPage from '../pages/KeranjangPage';
import PemesananPage from '../pages/PemesananPage';
import PemesananDetailPage from '../pages/PemesananDetailPage';
import DetailItemPage from '../pages/DetailItemPage';
import ChatPage from '../pages/ChatPage';

class RootApp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      userContext: {
        authedUser: null,
        setAuthedUser: (authedUser) => {
          this.setState((state)=>{
            return {
              userContext:{
                ...state.userContext,
                authedUser
              }
            }
          })
        }
      }
    };
  }

  async componentDidMount() {
    M.AutoInit();
    const getSelfUserReq = await getSelfUser();
    // const { data } = null;
    this.setState((state) => {
      return {
        userContext:{
          ...state.userContext,
          authedUser: null
        }
      };
    });
    
    if(getSelfUserReq.success)
      this.setState((state) => {
        return {
          userContext:{
            ...state.userContext,
            authedUser: getSelfUserReq.data,
          }
        };
      });
  }
  
  render() {
      return (
        <UserProvider value={this.state.userContext}>
            <Navigation/>
            <div className="container" id="content">
              <Routes>
                <Route path="/" element={<HomePage/>}/>
                <Route path="/menu" element={<MenuKateringPage/>} />
                <Route path="/menu/:menuId" element={<MenuDetailPage/>} />
                {(()=>{
                  if(this.state.userContext.authedUser === null)
                    return (<>
                      <Route path="/login" element={<LoginPage/>} />
                      <Route path="/register" element={<RegisterPage />} />
                    </>)
                  return (<>
                    <Route path="/keranjang" element={<KeranjangPage/>} />
                    <Route path="/pemesanan" element={<PemesananPage/>} />
                    <Route path="/pemesanan/:pemesananId" element={<PemesananDetailPage/>} />
                    <Route path="/pemesanan/:pemesananId/detail" element={<DetailItemPage/>} />
                    <Route path="/pemesanan/:pemesananId/chat" element={<ChatPage/>} />
                    <Route path="/logout" element={<Logout/>} />
                  </>)
                })()}
              </Routes>
            </div>
            <Footer/>
        </UserProvider>
      )
  }
}

export default RootApp;