import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { addKeranjang } from '../utils/api';
import Loading from './Loading';

const KeranjangInput = ({menuId}) => {
  const navigate = useNavigate();
  const [isLoading,setIsLoading] = useState(false);
  const [jumlah,setJumlah] = useState('');

  const onSubmitHandler = async (event) => {
    event.preventDefault();
    setIsLoading(true);

    const reqLogin = await addKeranjang({
      menu_id: menuId,
      jumlah,
    });

    setIsLoading(false);

    if(!reqLogin.success){
      M.toast({html: reqLogin.message});
      return
    }
    
    navigate('/keranjang');
  }

  if(isLoading) return <Loading/>

  return (
    <form onSubmit={onSubmitHandler} className='login-input'>
        <div className="col s4">
            <input type="number" placeholder='Jumlah' value={jumlah} onChange={(e)=>{ setJumlah(e.target.value) }} />
        </div>
        <div className="col s12">
            <button className="waves-effect waves-light btn-small">Masukkan Keranjang</button>
        </div>
    </form>
  );
}

export default KeranjangInput;