import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Collection from '../components/Collection';
import Loading from '../components/Loading';
import Message from '../components/Message';
import TitlePage from '../components/TitlePage';
import ImgBelumLunas from '../images/belum_lunas.png';
import ImgLunas from '../images/lunas.png';
import ImgVerif from '../images/verif.png';
import ImgSelesai from '../images/selesai.png';
import { getPemesananAll, getTotalByPemesananId } from '../utils/api';

const PemesananPage = () => {
  const titlePage = "Pemesanan";
  const iconPage = "book";
  const navigate = useNavigate();
  const [isLoading,setIsLoading] = useState(false);

  const [pemesanan,setPemesanan] = useState({}) 
  const [collectionItem,setCollectionItem] = useState([]) 

  useEffect(()=>{
    getPemesananAllFunc();
  },[])

  const getPemesananAllFunc = async () => {
        setIsLoading(true);
        const reqGetPemesananAll = await getPemesananAll();
        setPemesanan(reqGetPemesananAll);
        if(!reqGetPemesananAll.success){
            setIsLoading(false);
            return
        }
        setCollectionItem([]);
        await reqGetPemesananAll.data.map(async (item) => {
            const reqGetTotalByPemesananId = await getTotalByPemesananId(item.id);
            const total = reqGetTotalByPemesananId.data[0].total;
            let status = "Belum Lunas";
            let image = ImgBelumLunas;
            
            if(item.tgl_bayar != null && item.tgl_verif == null){
                status = "Lunas";
                image = ImgLunas;
            }

            if(item.tgl_verif != null && item.tgl_selesai == null){
                status = "Proses Verifikasi";
                image = ImgVerif;
            }

            if(item.tgl_selesai != null){
                status = "Selesai";
                image = ImgSelesai;
            }
            
            setCollectionItem((prevState)=>[
                ...prevState,{
                id: item.id,
                title: item.no_pesan,
                body: `Total : Rp.${total} ( ${status} )`,
                image,
                nav: (id) => {
                    navigate(id.toString())
                }
            }]);
        }
    )
    setIsLoading(false);
  }

  if(isLoading) return <Loading/>

  if(!pemesanan.success)
    return (<>
      <TitlePage titlePage={titlePage} iconPage={iconPage}/>
      <Message message={pemesanan.message}/>    
    </>)

  return (<>
    <TitlePage titlePage={titlePage} iconPage={iconPage}/>
    <Collection items={collectionItem}/>
  </>);
}

export default PemesananPage;