import React, { useEffect } from 'react';
import PropType from 'prop-types';

function ModalConfirm({ header, body, action, target }) {
    useEffect(()=>{
        var elems = document.querySelectorAll('.modal');
        M.Modal.init(elems);
    })

    return (
        <div id={target} className="modal">
            <div className="modal-content">
                <h4>{header}</h4>
                <p>{body}</p>
            </div>
            <div className="modal-footer">
                <a onClick={()=>action()} className="modal-close waves-effect waves-green btn-flat">Simpan</a>
                <a className="modal-close waves-effect waves-green btn-flat">Batal</a>
            </div>
        </div>
    );
}

ModalConfirm.propType = {
  header: PropType.string.isRequired,
  body: PropType.string.isRequired,
  textAction: PropType.string.isRequired,
};

export default ModalConfirm;