import { useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { addChatPelanggan, addKeranjang } from '../utils/api';
import Loading from './Loading';

const ChatInput = ({reloadParent}) => {
  const navigate = useNavigate();
  const [isLoading,setIsLoading] = useState(false);
  const {pemesananId} = useParams();
  const [chat,setChat] = useState('');

  const onSubmitHandler = async (event) => {
    event.preventDefault();
    setIsLoading(true);

    const addChatReq = await addChatPelanggan({
      pemesanan_id: pemesananId,
      chat,
    });

    setIsLoading(false);

    if(!addChatReq.success){
      M.toast({html: addChatReq.message});
      return
    }
    
    if(reloadParent) reloadParent();
  }

  if(isLoading) return <Loading/>

  return (
    <form onSubmit={onSubmitHandler} className='login-input'>
        <div className="col s4">
            <input type="text" placeholder='Ketik Chat' value={chat} onChange={(e)=>{ setChat(e.target.value) }} />
        </div>
        <div className="col s12">
            <button className="waves-effect waves-light btn-small">Kirim</button>
            <button onClick={()=>navigate(`/pemesanan/${pemesananId}`)} className="waves-effect waves-light btn-small">Kembali</button>
        </div>
    </form>
  );
}

export default ChatInput;